.bg {
    background: rgb(28, 28, 28);
    height: 100%;
}

.home-container {
    height: 100vh;
}

.home-center {
    
    width: auto;
    text-align: center;
    padding-top: 7vh;
}

.home-title {
    font-weight: bold;
    font-size: clamp(6vw, 2rem, 20rem);
    color: tomato;
}

.home-image {
    display: block;
    padding-top: 1rem;
    margin-left: auto;
    margin-right: auto;
    width: 30%;
    min-width: 20rem;
}

