.mission-text {
    margin-top: 1rem;
    color: darkkhaki;
    font-size: 1.5rem;
    padding: .5rem .5rem .5rem;
    text-align: center;
}

.mission-header {
    color: darkkhaki;
    font-size: 2rem;  
    padding: .5rem .5rem .5rem;
    text-align: center;
}

.mission-container {
    width: 60vw;
    min-width: 20rem;
    margin: auto;
    margin-top: 2rem;
}
.newsletter-container {
    margin: auto;
    width: 40vw;
    min-width: 20rem;
    text-align: center;
    padding-top: 12vh;
}

.newsletter-header {
    color: tomato;
}

.newsletter-button {
    background-color: tomato;
    border-color: darkkhaki;
    border-radius: 10px;
    padding: 0.5vw 4vw 1vw;
    font-size: 3vh;
    width: 45%;
    margin-top: 1.5rem;
    margin-bottom: 5rem;
    height: fixed;
    font-weight: bold;
}

.newsletter-button:hover {
    background-color: darkkhaki;
    border-color: tomato;

}

.newsletter-input {
    background: transparent;
    border-color: tomato;
    border-radius: 10px;
    height: 60px;
    width: 20vw;
    min-width: 20rem;
    padding: 0px 30px 0px;
    font-size: 1.4rem;
    color:rgb(214, 214, 214)
}

