.sm-icon {
   padding-left: 3.5%;
   padding-right: 3.5%;
}

.sm-container {
    margin: auto;
    display: flex;
    justify-content: center;
}

.sm-wrapper {
    margin-top: 1rem;
    justify-content: center;
    
    background-color: darkkhaki;
    border-radius: 10px;
    border: 3px solid black;
    width: 30vw;
    min-width: 20rem;
    margin: auto;
    margin-top: 3rem;
    margin-bottom: 3rem;
}

.sm-icon:hover {
    fill: tomato;
}

.car-container {
    width: 90%;
    max-width: 750px;
    margin: auto;
    padding-top: 5rem;
    padding-bottom: 3rem;
}

.car-container-item {
    width: 75%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.car-container-img {
    width: 35%;
    border-radius: 1000px;
}

.car-container-text {
    height: 9rem;
}

